.min-max(@min-width, @max-width, @padding) {
  min-width: @min-width;
  max-width: @max-width;
  padding: 0 @padding;
  box-sizing: border-box;
  margin: 0 auto;
}

.btn (@bg: @black, @fs: 9px, @lh: 13px, @ls: 0.54px, @br: 40px, @w: 114px, @clr: @white) {
	width: @w;
	color: @clr;
	font-size: @fs;
	line-height: @lh;
	letter-spacing: @ls;
	background-color: @bg;
	border-radius: @br;
	padding: 13px 0;
	display: block;
	text-align: center;
	text-transform: uppercase;
	margin: 25px 0;
	text-decoration: none;

	 &:hover {
    background-color: lighten(@bg, 15%);
  }
}